export default "import { Component, OnDestroy } from \"@angular/core\";\n\n@Component({\n    selector: \"nui-stacked-header-example\",\n    templateUrl: \"./stacked-header.example.component.html\",\n})\nexport class StackedHeaderProgressExampleComponent implements OnDestroy {\n\n    public show = false;\n    public percent = 0;\n    public isCanceled = false;\n    private stop: any = undefined;\n\n    public startProgress() {\n        this.clearInterval();\n\n        this.show = true;\n        this.stop = setInterval(() => {\n            if (this.percent < 100) {\n                this.percent += 10;\n            } else {\n                this.onCancel();\n            }\n        }, 1000);\n    }\n\n    public onCancel() {\n        this.clearInterval();\n        this.isCanceled = true;\n        this.show = false;\n        this.stop = undefined;\n        this.percent = 0;\n    }\n\n    public ngOnDestroy() {\n        this.clearInterval();\n    }\n\n    private clearInterval() {\n        if (this.stop) {\n            clearInterval(this.stop);\n        }\n    }\n}\n";