export default "import { Component, OnDestroy } from \"@angular/core\";\n\n@Component({\n    selector: \"nui-indeterminate-progress-example\",\n    templateUrl: \"./indeterminate-progress.example.component.html\",\n})\nexport class IndeterminateProgressExampleComponent implements OnDestroy {\n    public show = false;\n    public isCanceled = false;\n    private stop: any = undefined;\n\n    public startProgress() {\n        this.clearInterval();\n        this.show = true;\n    }\n\n    public onCancel() {\n        this.clearInterval();\n        this.isCanceled = true;\n        this.show = false;\n        this.stop = undefined;\n    }\n\n    public ngOnDestroy() {\n        this.clearInterval();\n    }\n\n    private clearInterval() {\n        if (this.stop) {\n            clearInterval(this.stop);\n        }\n    }\n}\n";