export default "import {Component, OnDestroy} from \"@angular/core\";\n\n@Component({\n    selector: \"nui-progress-with-help-template-example\",\n    templateUrl: \"./progress-with-help-template-example.component.html\",\n})\nexport class ProgressWithHelpTemplateExampleComponent implements OnDestroy {\n\n    public show = false;\n    public percent = 0;\n    private intervalId?: NodeJS.Timeout;\n\n    public toggleProgress() {\n        this.show = !this.show;\n\n        if (!this.show) {\n            this.clearInterval();\n            this.percent = 0;\n        } else {\n            this.intervalId = setInterval(() => {\n                if (this.percent < 100) {\n                    this.percent += 10;\n                } else {\n                    this.clearInterval();\n                    this.show = false;\n                }\n            }, 1000);\n        }\n    }\n\n    public ngOnDestroy() {\n        this.clearInterval();\n    }\n\n    private clearInterval() {\n        if (this.intervalId) {\n            clearInterval(this.intervalId);\n            this.intervalId = undefined;\n        }\n    }\n}\n";